.section-clients-block {
    padding: 46px 0 20px 0;

    @media screen and (min-width: $w576) {
        padding: 90px 0 50px 0;
    }

    &.section-clients-block--blue {
        background-color: var(--color-bg-2);
        padding: 30px 0 50px 0;

        & .clients-marquee {
            &__list {
                & li {
                    background-color: var(--color-bg-2);

                    & .bgimage {
                        mix-blend-mode: multiply;
                    }
                }
            }
        }
    }
}

.clients-block {



    &__top {
        @media screen and (min-width: $w1200) {
            display: flex;
            padding-bottom: 12px;
        }
    }

    &__left {
        @media screen and (min-width: $w1200) {
            width: 32%;
        }
    }

    &__right {

        font-size: 16px;
        line-height: 145%;

        @media screen and (min-width: $w1200) {
            width: 68%;
            padding-left: 36px;
        }
    }

    &__title {
        @include font-h4;
        padding-bottom: 0.55em;

        @media screen and (min-width: $w760) {
            @include font-h2;
        }
    }

    &__link {

        padding-bottom: 8px;

        & a {
            color: var(--color-accent-1);
            font-size: 16px;
            display: flex;
            align-items: center;

            & svg {
                margin-left: 12px;
                fill: var(--color-accent-1);
            }

            &:hover {
                color: var(--color-accent-2);

                & svg {
                    fill: var(--color-accent-2);
                }
            }
        }
    }
}

.clients-marquee {
    background-color: var(--black);
    padding: 21px 0;
    color: #fff;
    font-size: 12px;
    text-transform: uppercase;

    @media screen and (min-width: $w1440) {
        padding: 30px 0;
    }

    @media screen and (min-width: $w576) {
        font-size: 16px;
        padding: 24px 0;
    }

    &__list {
        // animation: marquee 12s linear infinite;

        & ul {
            display: flex;
            margin: -12px 0;
            transition: 0.31s linear;

            & li {
                padding: 12px;
                white-space: nowrap;


                & .bgimage {
                    @include bgimage-contain;
                    width: 200px;
                    padding-top: 60%;
                }
            }
        }
    }
}